@import "~slick-carousel/slick/slick.css";
@import "~slick-carousel/slick/slick-theme.css";

.Carousel {
  width: 100vw;
  position: absolute;
  z-index: 0;
}

.slide {
  width: 100vw;
  min-height: 300px;
  height: 80vh;
  background-color: #5C6BC0;
  display: -ms-flexbox !important;
  display: flex !important;
  -ms-flex-pack: center;
      justify-content: center;
  -ms-flex-align: center;
      align-items: center;
  overflow: hidden;
}

.slide > img {
  position: relative;
  opacity: 0.25;
  max-height: auto;
  max-width: auto;
  -webkit-filter: blur(3px);
          filter: blur(3px);
}


